
"use client";

import * as React from 'react';
import Link from 'next/link';
import AppLayout from '@/components/app-layout';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { BookOpen, Shapes, FileCheck, ArrowRight, Loader2 } from 'lucide-react';
import { collection, query, where, getCount, onSnapshot, orderBy, limit } from "firebase/firestore";
import { db, auth } from "@/lib/firebase";
import { useAuthState } from 'react-firebase-hooks/auth';
import type { Question } from '@/types';

export default function DashboardPage() {
  const [user] = useAuthState(auth);
  const [stats, setStats] = React.useState([
    { title: 'Total Questions', value: '0', icon: BookOpen },
    { title: 'Subjects', value: '0', icon: Shapes },
    { title: 'Exams Completed', value: '0', icon: FileCheck },
  ]);
  const [recentQuestions, setRecentQuestions] = React.useState<Question[]>([]);
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    if (!user) {
      setLoading(false);
      return;
    };

    const fetchStats = async () => {
        try {
            const questionsRef = collection(db, 'users', user.uid, 'questions');
            const subjectsRef = collection(db, 'users', user.uid, 'subjects');
            const examsRef = collection(db, 'users', user.uid, 'exams');

            const questionsSnap = await getCount(questionsRef);
            const subjectsSnap = await getCount(subjectsRef);
            const examsSnap = await getCount(query(examsRef, where('submitted', '==', true)));

            setStats([
                { title: 'Total Questions', value: String(questionsSnap.data().count), icon: BookOpen },
                { title: 'Subjects', value: String(subjectsSnap.data().count), icon: Shapes },
                { title: 'Exams Completed', value: String(examsSnap.data().count), icon: FileCheck },
            ]);
        } catch (error) {
            console.error("Error fetching stats: ", error);
        } finally {
            setLoading(false);
        }
    }

    const q = query(collection(db, 'users', user.uid, 'questions'), orderBy('createdAt', 'desc'), limit(5));
    const unsubscribe = onSnapshot(q, (querySnapshot) => {
        const questionsData = querySnapshot.docs.map(doc => {
            const data = doc.data();
            return {
                id: doc.id,
                ...data,
                createdAt: (data.createdAt as any)?.toDate() ?? new Date()
            } as Question;
        });
        setRecentQuestions(questionsData);
    });

    fetchStats();
    return () => unsubscribe();
  }, [user]);


  return (
    <AppLayout>
      <div className="space-y-8">
        <div>
          <h1 className="text-3xl font-bold font-headline">Welcome back!</h1>
          <p className="text-muted-foreground">Here&apos;s a snapshot of your progress.</p>
        </div>
        
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {stats.map((stat) => (
            <Card key={stat.title}>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">{stat.title}</CardTitle>
                <stat.icon className="h-5 w-5 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                {loading ? <Loader2 className="h-6 w-6 animate-spin" /> : <div className="text-2xl font-bold">{stat.value}</div>}
              </CardContent>
            </Card>
          ))}
        </div>

        <div className="grid gap-8 md:grid-cols-2 lg:grid-cols-7">
          <Card className="lg:col-span-4">
            <CardHeader>
              <CardTitle>Recent Questions</CardTitle>
              <CardDescription>A look at the latest questions you&apos;ve added.</CardDescription>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Question</TableHead>
                    <TableHead>Subject</TableHead>
                    <TableHead className="text-right">Difficulty</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {loading ? (
                    <TableRow>
                        <TableCell colSpan={3} className="text-center h-24"><Loader2 className="h-6 w-6 animate-spin mx-auto" /></TableCell>
                    </TableRow>
                  ) : recentQuestions.length > 0 ? (
                    recentQuestions.map((q) => (
                      <TableRow key={q.id}>
                        <TableCell className="font-medium truncate max-w-xs">{q.question}</TableCell>
                        <TableCell>{q.subject}</TableCell>
                        <TableCell className="text-right">
                          <Badge variant={q.difficulty === 'easy' ? 'secondary' : q.difficulty === 'medium' ? 'outline' : 'default'} className="capitalize">{q.difficulty}</Badge>
                        </TableCell>
                      </TableRow>
                    ))
                  ) : (
                     <TableRow>
                        <TableCell colSpan={3} className="text-center h-24">No recent questions.</TableCell>
                      </TableRow>
                  )}
                </TableBody>
              </Table>
              <Button variant="outline" size="sm" className="mt-4 w-full" asChild>
                <Link href="/questions">View All Questions <ArrowRight className="ml-2 h-4 w-4" /></Link>
              </Button>
            </CardContent>
          </Card>

          <Card className="lg:col-span-3">
            <CardHeader>
              <CardTitle>Quick Actions</CardTitle>
              <CardDescription>Jump right back into learning.</CardDescription>
            </CardHeader>
            <CardContent className="flex flex-col space-y-4">
              <Button size="lg" asChild>
                <Link href="/questions/new">Add New Question</Link>
              </Button>
              <Button size="lg" variant="secondary" asChild>
                <Link href="/exams/new">Generate a New Exam</Link>
              </Button>
            </CardContent>
          </Card>
        </div>
      </div>
    </AppLayout>
  );
}
